import React from 'react';
import {Link, useLocation} from 'react-router-dom';
import '../css/navbar.css';

interface NavbarContentProps{
	title: string
	currentPath: string
}

const NavbarContent: React.FC<NavbarContentProps> = (props) => {
	const [style, setStyle] = React.useState({});
	React.useEffect(() => {
		if(props.currentPath === props.title.toLowerCase() || (props.currentPath === "" && props.title === "Home")){
			setStyle({color: "yellow"});
		}
		else setStyle({color: "white"});
	}, [props.currentPath, props.title]);
	
	return (
		<div className = "navbar-contents">
			<Link className = "navbar-text" to = {props.title === "Home" ? "/" : props.title.toLowerCase()} style = {style}>{props.title}</Link>
		</div>
	);
}

const Navbar: React.FC = () => {
	const currentPath = useLocation().pathname.substr(1);
	return (
		<nav className = "navbar">
			<NavbarContent title = "Home" currentPath = {currentPath}/>
			<NavbarContent title = "News" currentPath = {currentPath}/>
			<NavbarContent title = "Movie" currentPath = {currentPath}/>
			<NavbarContent title = "Cast" currentPath = {currentPath}/>
			<NavbarContent title = "Photo" currentPath = {currentPath}/>
		</nav>
	);
}

export default Navbar;