import React from 'react'
import Modal from 'react-modal';
import '../css/cast.css'

interface CastBoxProps{
	name: string,
	role: string,
	imgPath: string,
	description: string
}

const CastBox: React.FC<CastBoxProps> = (props) => {

	const [modalShowed, setModalShowed] = React.useState(false);
	const openModal = React.useCallback(() => {
		setModalShowed(true);
	}, []);
	const closeModal = React.useCallback(() => {
		setModalShowed(false);
	}, [])

	const Ret: React.FC = () => {
		return (
			<button className = "castbox" onClick = {openModal}>
				<div className = "castbox-name">{props.name}</div>
				<img className = "castbox-img" src = {props.imgPath} alt = "" />
				<div className = "castbox-description">{props.role}</div>
			</button>
		);
	}
	if(modalShowed){
		return (
			<>
			<Ret />
				<Modal
					isOpen = {modalShowed}
					onRequestClose = {closeModal}
					style = {{
						content: {
							top: "0px",
							left: "0px",
							bottom: "0px",
							right: "0px",
							height: "400px",
							width: "300px",
							margin: "auto",
							backgroundColor: "black"
						},
						overlay: {
							background: "rgba(128,128,128,0.75)"
						}
					}}
				>
					<div className = "modal-main">
						<div className = "modal-name">{props.name}</div>
						<img src = {props.imgPath} className = "modal-img" alt = "" />
						<br />
						<div className = "modal-description">
							<p>{props.description}</p>
						</div>
					</div>
				</Modal>
			</>
		);
	}
	return <Ret />;
}

const Cast: React.FC = (props) => {
	return (
		<div className = "main-container">
			<div className = "main-contents">
				<div className = "castboxes">
					<div className = "castbox-kind">筑駒革命軍</div>
					<CastBox 
						name = "星野 佑希"
						role = "筑駒革命軍のリーダー"
						imgPath = "/img/casts/hoshino.jpg"
						description = {`筑駒革命軍のリーダー。自治会が文化祭を廃止したことに疑問を感じ、筑駒革命軍を結成する。`}
					/>
					<CastBox 
						name = "松下 秀輝"
						role = "筑駒革命軍のブレーン"
						imgPath = "/img/casts/matsushita.jpg"
						description = {`星野の親友。革命軍の中枢として、作戦立案などを担う。`}
					/>
					<CastBox 
						name = "本田 真"
						role = "筑駒革命軍の中心人物"
						imgPath = "/img/casts/honda.jpg"
						description = {`星野の親友。情熱的な性格で、行動力がある。`}
					/>
					<div className = "castbox-kind">筑駒防衛軍</div>
					<CastBox
						name = "三好 政彦"
						role = "筑駒自治会長"
						imgPath = "/img/casts/miyoshi.jpg"
						description = {`筑駒自治会長。その厳格な風貌と威厳ある振る舞いによって、生徒からは恐れられている。`}
					/>
					<CastBox
						name = "角田 直大"
						role = "筑駒自治会副会長"
						imgPath = "/img/casts/kakuta.jpg"
						description = {`筑駒自治会副会長。三好のように恐れられてはいないが、規律意識が強い。`}
					/>
					<CastBox
						name = "大島 健"
						role = "筑駒自治会会計"
						imgPath = "/img/casts/oshima.jpg"
						description = {`筑駒自治会会計。比較的冷静な性格。星野たちとは同じクラス。`}
					/>
					<CastBox
						name = "福本 洋"
						role = "筑駒自治会書記"
						imgPath = "/img/casts/fukumoto.jpg"
						description = {`筑駒自治会書記。怒りっぽい性格。星野たちとは同じクラス。`}
					/>
					<div className = "castbox-kind">その他</div>
					<CastBox
						name = "鳥井"
						role = "2年2組担任"
						imgPath = "/img/casts/torii.jpg"
						description = {`星野たちのクラスの担任。`}
					/>
				</div>
			</div>
		</div>
	);
}

export default Cast;